*{
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.result {
  margin-left: 50px;
  margin-right: 50px;
  padding-top: 20px;
  border-top: 2px solid #ddd;
}

.result h3, .result h4 {
  font-weight: normal;
  font-size: 20px;
  color: #000;
  text-shadow: 0 0 0.2em white, 0 0 0.2em white,
        0 0 0.2em white;
}
.result em {
  font-weight: bold;
}

.card{
  width: 60%;
  display: block;
  padding: 20px;
  box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
  background-image: url(/restapi/static/media/weather.a15342d7.jpg);
  margin: 20px auto;
}

@media (max-width: 1200px) {
    .card{
      width: 100%;
    }
}
@media (max-width: 400px) {
  .result h3, .result h4 {
    font-size: 19px;
}
}
.App input, .App button {
  display: block;
  margin: 20px auto;
  padding: 10px 20px;
  border: 2px solid gray;
  background-color: #fff;
  transition: .3s;
  font-size: 30px;
}
.App input{
 width: 60%;
}
.App button {
  cursor: pointer;
  width: 40%;
}

.App button:hover {
  background-color: #000;
  color: white;
  border-color: black;
}

.App{
  width:60%;
  margin: auto;
}

@media (max-width: 900px) {
  .App button {
    width: 70%;
  }
}

@media (max-width: 800px){
.App{
  width: 80%;
}
}
@media (max-width: 500px){
.App{
  width: 100%;
}
}
@media (max-width: 400px){
  .App input, .App button {
    font-size: 25px;
  }
}

